import React, { Component } from 'react';
import './App.css';
import FormGroup  from 'react-bootstrap/lib/FormGroup'
import ControlLabel  from 'react-bootstrap/lib/ControlLabel'
import FormControl from 'react-bootstrap/lib/FormControl'

const marked = require("marked");

class App extends Component {

state ={
  markdown:""
}

updateMarkdown = function(markdown) {
  this.setState({markdown});
}

  render() {
    let {markdown} = this.state;
    console.log({markdown})
    return (

     <div className="App">
       <br />
       <div class ="container-fluid">

       <header class="App-header">
        <div class="App-title"><p class="title">MARKDOWN</p></div>
        <p id="copyright">coded by Rafael Pessoa</p>
       </header>

       <br/><br/>
      <div class="row">
      <div class="col-md-6 col-sm-12">
       <FormGroup controlId="formControlsTextArea">
       <ControlLabel><h5>Markdown input</h5></ControlLabel>
       <FormControl id="input" componentClass="textarea" placeholder="Enter markdown here" value={markdown} onChange={(event)=>this.updateMarkdown(event.target.value)}></FormControl>
       </FormGroup>
      </div>

      <div class="col-md-6 col-sm-12">
        <h5>Git Flavoured Markdown output</h5>
        <div id="back" dangerouslySetInnerHTML = {{__html:marked(markdown)}}></div>
      </div>
      </div>
      </div>
     </div>
    );
  }
}


export default App;



